<!-- html -->
<template>
  <div class="answer">
    <!---->
    <head-title class="title" title=" "></head-title>
    <question-choice class="question" key="question" v-for="item in items" :object="item" :tag="item.questionTag" :selectType="item.type" v-on:choiceAction="answerAction"></question-choice>
    <!---->
    <div class="submit" @click="submitAction">
      <div class="submitContent"><p style="color: white; font-size: 2.5vh"></p></div>
    </div>
  </div>
</template>

<!-- js -->
<script>
import HeadTitle from "../components/custom/headTitle.vue";
import QuestionChoice from "../components/custom/questionChoice.vue";
export default {
    components: {HeadTitle, QuestionChoice},
    name: 'answer',
    data () {
      return {
          items: [
              {title: "1.", data: [{title: ""}, {title: ""}, {title: ""}], type: "selectTypeSingle", questionTag: "1"},
              {title: "2.", data: [{title: ""}, {title: ""}, {title: ""}, {title: ""}], type: "selectTypeMulti", questionTag: "2"},
              {title: "3.", data: [{title: ""}, {title: ""}, {title: ""}, {title: ""}], type: "selectTypeMulti", questionTag: "3"}
          ]
     }
    },
    methods: {
        submitAction: function () {
            console.log("submitAction")
        },
        answerAction: function (answer) {
            console.log(answer);
        }
    },
    watch: {
    },
    created () {
        console.log(this.$route.query.user_id);
    }
}
</script>
<!-- css -->
<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  .answer {
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    position: fixed;
    display: flex;
    flex-direction: column;
    align-items: center;
    background-image: url("../assets/backTwo.jpg");
    background-size: 100% 100%;
  }
  .title {
    margin-top: 4vh;
  }
  .question {
    margin-top: 3vh;
  }
  .submit {
    margin-top: 2vh;
    width: 85%;
    height: 8vh;
    background-color: black;
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  .submitContent {
    height: 100%;
    display: flex;
    flex-direction: row;
    align-items: center;
  }
</style>



