<!-- html -->
<template>
  <div class="verification">
    <img src="../../assets/code.png" class="photo">
    <div class="line"></div>
    <input type="text" name="code" placeholder='' class='codeInput' v-model='code' @change='codeChangeAction'>
    <div class="line_second"></div>
    <div class="sendCode" @click='sendCodeAction'> {{sendText}} </div>
  </div>
</template>

<!-- js -->
<script>
export default {
  name: 'verification',
  data () {
    return {
      sendText: '',
      code: '',
      second: 60,
      selected: true
    }
  },
  methods: {
    codeChangeAction: function () {
      //  param: 1 2
      this.$emit('codeNumChange', this.code);
    },
    //  
    sendCodeAction: function () {
      // 
      this.$emit('sendAction');
      //  control   =  
      // if (this.selected) {
      //   this.start();
      // }
      // this.selected = false;
    },
    // 
    start: function () {
      // console.log(this.second)
      this.time = this.second;
      this.timer();
    },
    // 
    stop: function () {
      this.time = 0;
      this.disabled = false;
    },
    // 
    timer: function () {
      if (this.time > 0) {
        this.time--;
        setTimeout(this.timer, 1000);
        this.sendText = this.time + 's';
      } else {
        this.sendText = '';
        this.selected = true;
      }
    }
  }
}
</script>

<!-- css -->
<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.verification {
	height: 3.5rem;
	width: 85%;
	/*background-color: red;*/
  display: flex;
  flex-direction: row;
  align-items: center;
  border-style: solid;
  border-width: 0.05rem;
  border-color: black;
}
.photo {
  margin-left: 0.7rem;
  height: 1.8rem;
  width: 1.8rem;
}
.line {
  margin-left: 1rem;
  height: 1.8rem;
  width: 0.01rem;
  border:0.01rem dashed lightgray;
}
.codeInput {
  margin-left: 0.5rem;
  height: 80%;
  width: 44%;
  font-size: 4vw;
  border: none;
  outline: medium;
  background-color: transparent;
}
.line_second {
  margin-left: 0.1rem;
  height: 1.8rem;
  width: 0.1rem;
  background-color: lightgray;
  
}
.sendCode {
  width: 30%;
  margin-left: 0.2rem;
  font-size: 4vw;
  color: black;
  text-align: center;
}
</style>



