<!-- html -->
<template>
  <div class="shara">
    <!---->
    <head-title class="title" title=" "></head-title>

    <tab-container class="tabContainer" v-model="active" swipeable>
      <tab-container-item id="tab-container1">

      </tab-container-item>
    </tab-container>

    <div class="sharaButton" @click="sharaAction">
      <div class="buttonContent">
        <img class="sharaImg" src="../assets/shara.png"/>
        <div style="color: white; margin-left: 2vh; font-size: 2.5vh; font-weight: 900"></div>
      </div>
    </div>

  </div>
</template>

<!-- js -->
<script>
import HeadTitle from "../components/custom/headTitle.vue";
import { TabContainer, TabContainerItem} from 'mint-ui';
import wx from 'weixin-js-sdk';
export default {
    components: {HeadTitle, TabContainer, TabContainerItem},
    name: 'shara',
  data () {
    return {
        active: 'tab-container1',
    }
  },
  methods: {
      sharaAction: function () {
          wx.ready(function () {
              wx.chooseImage({
                  count: 1, // 9
                  sizeType: ['original', 'compressed'], // 
                  sourceType: ['album', 'camera'], // 
                  success: function (res) {
                      alert("success")
                  },
                  fail: function (error) {
                      alert(JSON.stringify(error))
                  }
              });
          })
      }
  },
  watch: {
  },
  created () {
      alert(location.href.split('#')[0])
  }
}
</script>
<!-- css -->
<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

  .shara {
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    position: fixed;
    display: flex;
    flex-direction: column;
    align-items: center;
    background: url("../assets/backOne.jpg");
    background-size: 100% 100%;
  }
  .title {
    margin-top: 4vh;
  }
  .tabContainer {
    margin-top: 3vh;
    width: 100%;
    height: 75vh;
    overflow-y: auto;
    background-color: #895192;
  }
  .sharaButton {
    width: 100%;
    height: 8vh;
    position: fixed;
    bottom: 0;
    background-color: black;
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  .buttonContent {
    height: 100%;
    display: flex;
    flex-direction: row;
    align-items: center;
  }
  .sharaImg {
    width: 2.5vh;
    height: 2.5vh;
  }
</style>



